.tippy-tooltip {
    width: 260px;
    padding: 4px 6px;
    background-color: #fff;
    border: 1px solid #dcdcdc;
    border-radius: 4px;
    box-sizing: border-box;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
    font-size: 12px;
    line-height: 13px;
    color: #000;
}

.tooltip-content .tooltip-title {
  margin: 0;
  margin-bottom: 5px;
  font-family: "GT America Bold";
  font-weight: 400;
}

.tooltip-content .tooltip-table {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
}

.tooltip-content .tooltip-table tbody tr {
  border-top: 0.5px solid #d6d6d6;
}

.tooltip-content .tooltip-table th {
  padding: 5px;
  padding-top: 0px;
  font-family: "GT America Condensed";
  font-weight: 400;
  text-align: right;
  text-transform: uppercase;
  vertical-align: bottom;
}

.tooltip-content .tooltip-table td {
  width: 60px;
  padding: 5px;
  text-align: right;
  text-transform: capitalize;
  font-variant-numeric: tabular-nums;
}

.tooltip-content .tooltip-table td:first-child {
  padding-left: 0;
  text-align: left;
}

.tooltip-content .tooltip-table td:last-child {
  padding-right: 0;
}

.tooltip-content .tooltip-table th:first-child {
    padding-left: 0;
    text-align: left;
  }
  
.tooltip-content .tooltip-table th:last-child {
  padding-right: 0;
}

.tooltip-content .tooltip-table .row-header {
  text-align: left;
  width: 80px;
}

/* these are custom breakpoints to make the svg work well on phones */
 @media only screen and (max-width: 670px) {
   .tippy-popper {
      position: fixed !important;
      transform: none !important;
      max-width: none;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      margin: 0;
   }

    .tippy-tooltip {
      position: absolute;
      max-width: none !important;
      width: 100%;
      top: auto !important;
      left: 0 !important;
      bottom: 0;
      right: 0;
      margin: 0;
      padding: 20px;
      background-color: #000;
      border: none;
      border-top: 1px solid #FFF;
      border-radius: 0;
      box-shadow: none;
      color: #FFF;
    }

    .tooltip-content .tooltip-title {
      margin-bottom: 17px;
    }
  
    .tooltip-content .tooltip-table tbody tr {
      border-top: none;
    }
  
    .tooltip-content {
      font-size: 13px;
    }

    .tooltip-content .tooltip-table td:not(:first-child) {
      font-family: "GT America Bold";
    }
  }